import React, { useState, useEffect } from 'react';
import { provider, getInstanceTriangle, addressExportTriangle } from '../Web3Util'

import Header from '../partials/Header';
import Footer from '../partials/Footer';
import Web3 from 'web3';

function NodePage() {
  let hasEthereum = window.ethereum != undefined
  const [contractInstanceTriangle, contractUpdaterTriangle] = useState(null)
  const [userNodeItems, userNodeItemsUpdater] = useState([])
  const [triangleToken, triangleTokenUpdater] = useState(0)
  const [selectedGasTokenAmount, selectedGasTokenAmountUpdater] = useState(0)
  const [contractBalance, contractBalanceUpdater] = useState(0)
  const [activeStakes, activeStakesUpdater] = useState(0)
  const [userAllowance, userAllowanceUpdater] = useState(0)


  useEffect(async () => {

    if (contractInstanceTriangle == null) {
      let contractInstanceTriangleNew

      let web3
      if (window.user != undefined) {
        web3 = new Web3(window.ethereum);
      } else {
        web3 = new Web3(provider);
      }
      contractInstanceTriangleNew = await getInstanceTriangle(web3)

      await contractUpdaterTriangle(contractInstanceTriangleNew)

      await loadData(contractInstanceTriangleNew)
    }
  });

  function onChangeGasToken(event) {
    selectedGasTokenAmountUpdater(event.target.value)
  }

  async function createNodeTriangle() {
    if (selectedGasTokenAmount <= 0) {
      alert("must send a value greater than 0")
      return
    }
    try {
      let web3 = new Web3(window.ethereum);
      let contractInstanceTriangleNew = await getInstanceTriangle(web3)
      await contractInstanceTriangleNew.methods.buyNodeForTriangles(Web3.utils.toWei(selectedGasTokenAmount)).send()
    } catch (e) {
      console.log("@@@ e", e)
    }
  }

  async function createNodeCurrency() {
    if (selectedGasTokenAmount <= 0) {
      alert("must send a value greater than 0")
      return
    }
    try {
      let web3 = new Web3(window.ethereum);
      let contractInstanceTriangleNew = await getInstanceTriangle(web3)
      await contractInstanceTriangleNew.methods.buyNodeForCurrency().send({
        value:Web3.utils.toWei(selectedGasTokenAmount)
      })
    } catch (e) {
      console.log("@@@ e", e)
    }
  }

  async function claimRewards(nodeId) {
    try {
      let web3 = new Web3(window.ethereum);
      let contractInstanceTriangleNew = await getInstanceTriangle(web3)
      await contractInstanceTriangleNew.methods.claimRewardsForNode(nodeId).send()
    } catch (e) {
    }
  }

  async function compoundRewards(nodeId) {
    try {
      let web3 = new Web3(window.ethereum);
      let contractInstanceTriangleNew = await getInstanceTriangle(web3)
      await contractInstanceTriangleNew.methods.compoundNode(nodeId).send()
    } catch (e) {
    }
  }

  async function approveTriangles() {
    try {
      let web3 = new Web3(window.ethereum);
      let contractInstanceTriangleNew = await getInstanceTriangle(web3)
      await contractInstanceTriangleNew.methods.approve(addressExportTriangle, "115792089237316195423570985008687907853269984665640564039457584007913129639935").send()
    } catch (e) {
    }
  }

  function timeout(ms) {
    return new Promise(resolve => setTimeout(resolve, ms));
  }

  async function sleep(fn, ...args) {
    await timeout(333);
    return fn(...args);
  }

  async function loadData(contractInstanceTriangle) {
    if (window.user != null) {
      let web3 = new Web3(window.ethereum);
      let userNodesCallArray = []
      let stakesBonusCallsArray = []
      let stakesRewardsCallsArray = []


      const [userCurrencyBalance, activeStakes, localUserStakeCounter, contractBalance,allowance] = await Promise.all([
        contractInstanceTriangle.methods.balanceOf(window.user).call(),
        contractInstanceTriangle.methods.activeStakes().call(),
        contractInstanceTriangle.methods.addressNodeCounter(window.user).call(),
        contractInstanceTriangle.methods.balanceOf(addressExportTriangle).call(),
        contractInstanceTriangle.methods.allowance(window.user,addressExportTriangle).call(),
      ])

      for (let counter = 0; counter < localUserStakeCounter; ++counter) {
        userNodesCallArray.push(contractInstanceTriangle.methods.addressToNodes(window.user, counter).call())
      }

      const localUserStakes = await Promise.all(userNodesCallArray)
      console.log("@@@ localUserStakes",localUserStakes)

      for (let counter = 0; counter < localUserStakes.length; ++counter) {
        // stakesBonusCallsArray.push(contractInstanceTriangle.methods.calculateBonus(localUserStakes[counter].id).call())
        stakesRewardsCallsArray.push(contractInstanceTriangle.methods.calculateRewardsForNode(localUserStakes[counter].id).call())
      }

      // const stakesBonus = await Promise.all(stakesBonusCallsArray)
      const stakesRewards = await Promise.all(stakesRewardsCallsArray)

      let localUserStakeItems = []
      let now = Date.now()
      now = now / 1000
      // console.log("@@@ now",now)

      for (let counter = 0; counter < localUserStakes.length; ++counter) {
        if (localUserStakes[counter].owner == window.user ) {
          localUserStakeItems.push(
            <div key={localUserStakes[counter].id} className="relative flex flex-col items-center p-6 bg-white rounded shadow-xl" data-aos="zoom-y-out" data-aos-delay="150">
              <div style={{ width: "100%" }}>
                <h4 className="text-xl font-bold leading-snug tracking-tight mb-1" style={{ float: "left" }}>Identifier - {localUserStakes[counter].id}</h4> <br />
                <h4 className="text-xl font-bold leading-snug tracking-tight mb-1" style={{ float: "left" }}>Triangles invested - {Number.parseFloat(Web3.utils.fromWei(localUserStakes[counter].trianglesInvested + "", 'ether'))} </h4> <br />

                <h4 className="text-xl font-bold leading-snug tracking-tight mb-1" style={{ float: "left" }}>Rewards for claim - {Number.parseFloat(Web3.utils.fromWei(stakesRewards[counter] + "", 'ether')).toFixed(18)}</h4> <br />
                <h4 className="text-xl font-bold leading-snug tracking-tight mb-1" style={{ float: "left" }}>Rewards for compound - {Number.parseFloat(Web3.utils.fromWei(stakesRewards[counter] + "", 'ether')).toFixed(18)*101/100}</h4> <br />


                {/* <h4 className="text-xl font-bold leading-snug tracking-tight mb-1" style={{ float: "left" }}>Stake bonus - {Number.parseFloat(Web3.utils.fromWei(stakesBonus[counter] + "", 'ether')).toFixed(18)}</h4> <br />
                <h4 className="text-xl font-bold leading-snug tracking-tight mb-1" style={{ float: "left" }}>Stake rewards - {Number.parseFloat(Web3.utils.fromWei(stakesRewards[counter] + "", 'ether')).toFixed(18)}</h4> <br />

                <h4 className="text-xl font-bold leading-snug tracking-tight mb-1" style={{ float: "left" }}>Total to claim - {Number.parseFloat(Web3.utils.fromWei(stakesBonus[counter] + "", 'ether')) + Number.parseFloat(Web3.utils.fromWei(stakesRewards[counter] + "", 'ether'))}</h4> <br /> */}

             
                <br /> <br />
                <br /> <br />
                <br /> <br />
                <h4 className="text-xl font-bold leading-snug tracking-tight mb-1" style={{ float: "left" }} onClick={() => claimRewards(localUserStakes[counter].id)}><button>Claim rewards</button></h4> <br />
                <h4 className="text-xl font-bold leading-snug tracking-tight mb-1" style={{ float: "left" }} onClick={() => compoundRewards(localUserStakes[counter].id)}><button>Compound rewards</button></h4> <br />

              </div>
            </div>
          )
        }
      }

      userNodeItemsUpdater(localUserStakeItems)
      userAllowanceUpdater(allowance);
      triangleTokenUpdater(userCurrencyBalance)
      activeStakesUpdater(activeStakes)
      contractBalanceUpdater(contractBalance)

    }

    await timeout(1000)
    await loadData(contractInstanceTriangle)

  }


  return (
    <div className="flex flex-col min-h-screen overflow-hidden">

      {/*  Site header */}
      <Header hasEthereum={hasEthereum} />

      {/*  Page content */}
      <main className="flex-grow">

        {/*  Page sections */}

        {/* <WalletBlocks bears={userNFTs}/> */}

        <section className="relative">
          {/* Section background (needs .relative class on parent and next sibling elements) */}
          <div className="absolute inset-0 top-1/2 md:mt-24 lg:mt-0 bg-gray-900 pointer-events-none" aria-hidden="true"></div>
          {/* <div className="absolute left-0 right-0 bottom-0 m-auto w-px p-px h-20 bg-gray-200 transform translate-y-1/2"></div> */}

          <div className="relative max-w-6xl mx-auto px-4 sm:px-6">
            <div className="py-12 md:py-20">

              {/* Section header */}
              <div className="max-w-3xl mx-auto text-center pb-12 md:pb-20">
                <h2 className="h2 mb-4">Wallet</h2>
                <p className="text-xl text-gray-600">View your $TRIANGLE balance and manage your nodes.</p>
              </div>

              {/* Items */}
              <div className="max-w-sm mx-auto grid gap-6 md:grid-cols-2 lg:grid-cols-4 items-start md:max-w-2xl lg:max-w-none">

                <p className="text-xl text-gray-600" style={{ textAlign: "center" }}>My triangle balance: <br />{Number.parseFloat(Web3.utils.fromWei(triangleToken + "", "ether")).toFixed(5)}</p>
                <p className="text-xl text-gray-600" style={{ textAlign: "center" }}>Total active stakes: <br />{activeStakes}</p>
                <p className="text-xl text-gray-600" style={{ textAlign: "center" }}>Total triangles invested: <br />{Number.parseFloat(Web3.utils.fromWei(contractBalance + "", "ether"))}</p>

              </div>

              <div className="">
                <button style={{ background: "white" }} onClick={createNodeTriangle}>
                  Create node with TRIANGLE
                </button>
                  &nbsp;
                <button style={{ background: "white" }} onClick={createNodeCurrency}>
                  Create node with AVAX
                </button>
                
                <br />
                <input onChange={onChangeGasToken} placeholder='Amount' type="number" />

              </div>

              <div className="max-w-sm mx-auto grid gap-6 md:grid-cols-2 lg:grid-cols-3 items-start md:max-w-2xl lg:max-w-none">

                {userNodeItems}

              </div>


            </div>
          </div>
        </section>

      </main>

      {/*  Site footer */}
      <Footer />

    </div>
  );
}

export default NodePage;